.main-page{
    font-family: 'Open Sans', sans-serif;
    /* background-color: #3d4a6e; */
    text-transform: none;
    width: 100%;
    height: 100%;
    position: relative;
}

/* responsive code */
/* 992px css */
@media screen and (max-width:992px){
    .main-page{
        height: auto;
        height: initial;
    }
}
.full_main_page{
    width: 100%;
    margin: 0 auto;
    position: relative;
}
.full_main_page .main_inner{
    width: 100%;
    margin: 0 auto;
    position: relative;
}
.main_inner_row.row{
    margin: 0;
    width: 100%;
    position: relative;
}
 .col-lg-12.col-md-12.col-sm-12.main_inner_col{
    padding: 0;
}
.left{
    float: left;
}
.right{
    float: right;
}

.clearfix{
    display: table;
    clear: both;
}


.header{
    width: 100%;
    margin: 0 auto;
    position: relative;
    top: 0;
    right: 0;
    padding: 0px 20px;
    left: 0;
    height: 75px;
    bottom: 0;
    float: left;
}
.header .header_container{
    width: 100%;
    margin: 0 auto;
    position: relative;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    float: left;
}

.header-inner{
    width: 100px;
    margin: 0 auto;
    display: inline-block;
    float: left;
}
.header-inner img{
    width: 100%;
    margin: 0 auto;
    float: left;
}
.header_router{
    width: 90%;
    margin: 0 auto;
    position: relative;
    /* float: right; */
    display: inline-block;
    top: 20px;
}
.header_router ul{
    width: 40%;
    margin: 0 auto;
    position: relative;
    list-style: none;
    color: gray;
    display: inline;
}
.header_router ul li{
    display: inline-block;
    color: gray;
    padding: 0 20px;
    font-size: 14px;
    text-align: center;
    font-weight: 500;
    letter-spacing: 1.5px;
}

.header_router ul li a{
   text-decoration: none;
    color: gray;
}
.header_search{
    display: inline-block;
    width: 37%;
    margin: 0 auto;
    position: relative;
    left: 10%;
}
.header_search input{
    width: 70%;
    margin: 0 auto;
    border-radius: 15px;
    border: none;
    padding: 7px;
    background-color: lightgray;
    outline: 0;
    padding-left: 20px;

}

.header_search i{
    font-size: 20px;
    color: skyblue;
    padding-left: 10px;
}
.carousel-inner img {
    width: 100%;
    height: 100%;
}

.slider{
    width: 100%;
    margin: 0 auto;
    float: left;
    position: relative;
    /* height: 100%; */
}

.slider .slider_inner{
    width: 100%;
    margin: 0 auto;
    float: left;
    position: relative;
    /* height: 100%; */
}
.footer_container{
    width: 100%;
    margin: 0 auto;
    float: left;
    position: relative;
}

footer { background-color:#0c1a1e; min-height:350px; font-family: 'Open Sans', sans-serif; }
.logofooter {height: 129px;font-size:25px; color:#fff; font-weight:700;}

.footer-col p { color:#fff; font-size:12px; font-family: 'Open Sans', sans-serif; margin-bottom:15px;}
.footer-col p i { width:20px; color:#999;}
.copyright { min-height:40px; background-color:#000000;}
.copyright p { text-align:left; color:#FFF; padding:10px 0; margin-bottom:0;}
.col-md-6.footer-col p {color: black;}
.bg{background-color: #e6e6e6;}
.dim{height: 146px;width: 31%;}
ul.tobottom{position: absolute;bottom:17%;left: 50%;}
.bg .container{padding-bottom: 26px;}


