.search-div{
    width: 100%;
    margin: 50px auto;
    position: relative;
    float: left;
    clear: both;
    text-align: center;

}

.search_heading{
    width: 65%;
    margin: 15px auto;
    text-align: left;
    color: gray;
    font-size: 18px;
  
}
.search_heading h3{
    font-size: 18px;
    letter-spacing: 1.5px;
}
.search_box{
   width: 65%;
    margin: 15px auto;
    position: relative;
}
.search_box input{
    width: 100%;
    display: inline-block;
    position: relative;
    border-radius: 15px;
    border: 1px solid gray;
    padding: 15px 10px;
    outline: 0;
    box-shadow: 0px 0px 16px 3px grey;
}

.search_box i{
    position: absolute;
    top: 16px;
    bottom: 0;
    right: 25px;
    font-size: 20px;
}
.tipEvent_container{
    width: 100%;
    margin: 0 auto;
    position: relative;
    float: left;
}

.event_tip{
    width: 100%;
    margin: 40px auto;
    position: relative;
}
.tip-heading{
    width: 100%;
    margin: 15px auto;
}
.tip-heading i{
    font-size: 20px;
    color: skyblue;
    display: inline-block;
}
.tip-heading h4{
    display: inline-block;
    color: gray;
    font-size: 20px;
    padding-left: 25px;
    letter-spacing: 1.5px
}
.col-lg-6.col-md-6.col-sm-12.tip-event-inner{
    width: 45%;
    max-width: 45%;
    margin: 0 auto;
}

.tip-img{
    width: 100%;
    border: 1px solid gray;
    margin: 0 auto;
    /* height: 300px; */
    border-radius: 10px;
    position: relative;
    box-shadow: 0px 0px 16px 3px grey;
}
.img-responsive.adjust{
    width: 100%;
    margin: 0 auto;
    border-radius: 9px;
    height: 365px;
